@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700&display=swap');
@import 'style/reset.scss';

body {
  font-family: 'Montserrat', sans-serif;
}

.hero {
  height: 100vh;
  @media (max-width: 720px) { height: 90vh; }
  width: 100vw;
  background-image: linear-gradient(to right, rgba(#081E34, 1), rgba(#01265C, .98), rgba(#243A5D, .90), rgba(#01265C, .30)),
    url('../img/hero_background.jpeg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
    //filter: blur(3px);
  //-webkit-filter: blur(3px);

  display: grid;
  align-items: center;
  @media (max-width: 720px) { justify-items: center; }
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;

  .imak-logo {
    @media (max-width: 720px) { max-width: 80vw; align-self: center; }
    margin-bottom: 5rem;
    padding: 2rem;
    width: 50rem;
    filter: drop-shadow(7px 7px 58px #090F1A);
  }
}